body {
  background-color: #262930;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.login {
  text-align: center;
}

.login a, .login button {
  color: azure;
  font-size: 20px;
  text-decoration: none;
}

button:enabled {
  color: #262930;
}

.login-logo {
  height: 15vmin;
  pointer-events: none;
}

.login-header {
  align-items: center;
  color: white;
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  justify-content: center;
  margin-top: 200px;
}

.game-header {
  background-color: aqua;
}

.game-header img {
  width: 50px;
}

.game-header span {
  margin: 0 5%;
}

.container {
  display: flex;
  flex-direction: column;
}

.question {
  color: lightseagreen;
  font-size: 110%;
  margin: 10% 30%;
}

.question-options {
  display: flex;
  flex-direction: column;
}

.question-options button {
  font-size: 150%;
  height: 35px;
  margin: 5px 0;
}

.question-right {
  border: 3px solid rgb(6, 240, 15);
}

.question-wrong {
  border: 3px solid red;
}

.next {
  align-self: center;
  height: 50px;
  width: 40%;
}

.timer {
  color: azure;
  font-size: 30px;
  text-align: center;
}

.results {
  color: azure;
  font-size: 200%;
  margin: 10% 30%;
}

.ranking {
  color: azure;
  font-size: 150%;
  margin: 10%;
}

.player {
  display: flex;
  height: 30px;
  justify-content: space-between;
  width: 50%;
}

.play-again {
  color: blue;
  margin: 20%;
  text-decoration: none;
}

.button-feedback {
  color: azure;
}
